'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import DesktopNavigation from './DesktopNavigation';
import MobileNavToggle from './MobileNavToggle';
import MobileSidebar from './MobileSidebar';

const Header = () => {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [isScrolled, setIsScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 10);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  const closeMobileMenu = () => {
    setIsMobileMenuOpen(false);
  };

  return (
    <>
      <header 
        className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 ${
          isScrolled 
            ? 'bg-white/95 backdrop-blur-md shadow-lg' 
            : 'bg-transparent'
        }`}
      >
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16 lg:h-20">
            
            {/* Mobile Menu Toggle - Left Side */}
            <div className="lg:hidden">
              <MobileNavToggle 
                isOpen={isMobileMenuOpen}
                onClick={toggleMobileMenu}
              />
            </div>

            {/* Logo */}
            <div className="flex-shrink-0">
              <Link href="/" className="flex items-center space-x-3">
                <div className="w-10 h-10 lg:w-12 lg:h-12 bg-blue-600 rounded-lg flex items-center justify-center">
                  <span className="text-white font-bold text-lg lg:text-xl">FG</span>
                </div>
                <div className="hidden sm:block">
                  <h1 className={`font-bold text-lg lg:text-xl transition-colors ${
                    isScrolled ? 'text-gray-900' : 'text-white'
                  }`}>
                    FG Science College
                  </h1>
                  <p className={`text-sm transition-colors ${
                    isScrolled ? 'text-gray-600' : 'text-gray-200'
                  }`}>
                    Excellence in Science Education
                  </p>
                </div>
              </Link>
            </div>

            {/* Desktop Navigation */}
            <div className="hidden lg:block">
              <DesktopNavigation isScrolled={isScrolled} />
            </div>

            {/* CTA Button */}
            <div className="flex-shrink-0">
              <Link 
                href="/admissions"
                className={`px-4 py-2 lg:px-6 lg:py-3 rounded-lg font-semibold text-sm lg:text-base transition-all duration-300 ${
                  isScrolled
                    ? 'bg-blue-600 text-white hover:bg-blue-700'
                    : 'bg-white text-blue-600 hover:bg-gray-100'
                }`}
              >
                Apply Now
              </Link>
            </div>
          </div>
        </div>
      </header>

      {/* Mobile Sidebar */}
      <MobileSidebar 
        isOpen={isMobileMenuOpen}
        onClose={closeMobileMenu}
      />
    </>
  );
};

export default Header;